:root{
    --title-font: 'Nunito', sans-serif;
    --text-font: 'Noto Sans Display', sans-serif;
    --white-color: #e0dddd;
    --primary-color: rgb(44, 103, 44);
    --primary-color-darker: rgb(35, 59, 22);
    --light-gray-color: #b0adad;
    --light-green-color: rgb(96, 175, 96);
    --red-color: rgb(132, 10, 10);
    --container-color: #323030;
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background-position-x: center;
  background-position-y: center;
  background-position: static;
  background-size: 100%;
  line-height: 1.8rem;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  align-items: center;
  z-index: 1;
  height: 60px;
  border-bottom: 0.1rem solid var(--light-gray-color);
  justify-content: center;
  background-color: var(--white-color);
  
}

.menu-content {
  display: flex;
  justify-content: space-between;
  padding-top: 0;
  padding-bottom: 0;
  height: 60px;
  align-items: center;
}

.menu ul {
  list-style: none;
  display: flex;
}

.menu ul li a {
  display: block;
  padding: 2rem;
  color: var(--primary-color-darker);
  font-size: 1.2rem;
  position: relative;
  text-decoration: none;
  font-family: var(--title-font);
}

.menu h1 {
  font-size: 1.7rem;
  color: var(--primary-color-darker);
  padding: 2rem;
  font-family: var(--title-font);
}

.menu h1 a {
  color: inherit;
}

.menu ul li a::after {
  content: '';
  position: absolute;
  bottom: 30px;
  left: 50%;
  width: 0;
  height: 0.1rem;
  background: var(--primary-color);
  transition: all 300ms ease-in-out;
}

.menu ul li a:hover::after {
  width: 50%;
  left: 25%;
}
.secao-padrao{
    min-height: 1100px;
    background-position: static;
    background-size: cover;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    padding: 200px 0 80px 0;
}

.texto-intro{
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.texto-intro h1 {
    font-size: 5rem;
    font-family: var(--title-font);
    color: var(--white-color);
    text-align: center;
    font-weight: 800;
    width: 29ch;
}

.texto-intro p {
    font-size: 2rem;
    font-family: var(--text-font);
    color: var(--white-color);
    text-align: center;
    margin-top: 20px;
    padding: 10px 100px 10px 100px;
    line-height: 2.5rem;
    font-weight: 400;
}
.container{
    display: flex;
}

.card {
  max-width: 450px;
  margin: 100px auto;
  background: var(--container-color);
  padding: 30px;
  border-radius: 10px;
  color: var(--white-color);
  display: flex;
    flex-direction: column;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.29);;
}

.card h2, .tools-container h2 {
    font-size: 2rem;
    font-family: var(--title-font);
    font-weight: 400;
    align-self: center;
    color: white;
}

.card p {
  font-weight: 400;
  font-size: 1.2rem;
  font-family: var(--text-font);
  color: var(--white-color);
  margin-bottom: 20px;
  align-self: center;
}
.card img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  margin-bottom: 20px;
}

.botao-padrao{
  padding: auto;
  border: none;
  background: linear-gradient(90deg, rgb(3, 110, 3), rgb(6, 81, 6));
  color: var(--white-color);
  font-size: 20px;
  height: 40px;
  cursor: pointer;
  border-radius: 50px;
  width: 150px;
  align-content: center;
  display: block;
  box-shadow: 0 4px 15px rgb(20, 67, 26);
  transition: box-shadow 0.3 ease, transform 0.3 ease;
  align-self: center;
  margin-top: auto;
}

.botao-padrao a {
  text-decoration: none;
  color: var(--white-color);
}

.botao-padrao:hover {
  background: linear-gradient(90deg, rgb(8, 127, 8), rgb(3, 87, 3));
  box-shadow: 0 10px 20px rgb(20, 67, 26, 0.5);
  transform: translateY(-1px);
}

.tools-container {
  max-width: 1000px;
  margin: auto;
  background: var(--container-color);
  padding: 30px;
  border-radius: 10px;
  color: var(--white-color);
  display: flex;
    flex-direction: column;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.29);;
} 
.tools-container p {
  font-weight: 400;
  font-size: 1.4rem;
  font-family: var(--text-font);
  color: var(--white-color);
  margin-bottom: 40px;
  align-self: center;
  padding: auto;
}

.tools-container h2 {
  font-size: 4rem!important;
}

 .container-envio * {
  display: block;
  width: 70%;
  margin-bottom: 10px;
  margin: auto;
}
.container-envio label {
  font-size: 24px;
  color: var(--white-color);
  font-family: var(--text-font);
  font-weight: 400;
  margin-bottom: 10px ;
}

.container-tabela{
  min-height: 200px;
  margin-top: 40px!important;
}

.container-tabela table {
  width: 100%;
  border-collapse: collapse;
  margin: none;
  color: var(--white-color);
  font-family: var(--text-font);
}

.container-tabela th, .container-tabela td {
  margin: 20px 0 0 0 !important;
  padding: 10px;
  text-align: center;
  border: var(--white-color) 2px solid;
  font-size: 1.6rem;
}

.input-padrao {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
  font-family: var(--text-font);
  font-weight: 400;
}

.btn-tarefa:focus {
  outline: 1px solid var(--primary-color);
}

.btn-envio {
  border: none;
  color: var(--white-color);
  font-size: 36px;
  font-weight: 400;
  height: 60px;
  cursor: pointer;
  margin-top: 40px;
  background: linear-gradient(90deg, rgb(3, 110, 3), rgb(6, 81, 6));
  box-shadow: 0 4px 15px rgb(20, 67, 26);
  transition: box-shadow 0.3 ease, transform 0.3 ease;
  border-radius: 50px;
  align-self: center;
  margin-bottom: auto;
}

.btn-tarefa:hover {
   background: linear-gradient(90deg, rgb(41, 186, 41), rgb(17, 133, 17));
  box-shadow: 0 4px 15px rgb(16, 54, 21);
  transform: translateY(-1px);
}
.checked-button,
.delete-button {
  height: 40px;
  margin: 10px;
  margin-top: 20px;
  color: var(--white-color);
  border: none;
  width: auto;
  cursor: pointer;
  font-size: 20px;
  font-family: var(--text-fonts);
  font-weight: 700;
  border-radius: 50px;
}

.checked-button {
  background: linear-gradient(90deg, rgb(32, 155, 32), rgb(15, 120, 15));
  box-shadow: 0 4px 15px rgb(20, 67, 26);
  transition: box-shadow 0.3 ease, transform 0.3 ease;
  margin-left: 40px; 
  padding: 0 15px 0 15px;
  font-family: var(--text-font);
  font-weight: 400;
}
.checked-button:hover{
   background: linear-gradient(90deg, rgb(41, 186, 41), rgb(17, 133, 17));
  box-shadow: 0 4px 15px rgb(16, 54, 21);
  transform: translateY(-1px);

}

.delete-button {
  background: linear-gradient(90deg,  rgb(151, 24, 24), rgb(91, 12, 12));
  box-shadow: 0px 4px 15px rgb(85, 28, 28);
  padding: 0 15px 0 15px;
  font-family: var(--text-font);
  font-weight: 400;
}

.delete-button:hover {
  background: linear-gradient(90deg, rgb(173, 30, 30), rgb(120, 16, 16));
  box-shadow: 0 4px 15px rgb(74, 24, 24);
  transform: translateY(-1px);
}

.checado {
  color: rgba(17, 244, 47, 0.63) !important;
  text-decoration: line-through !important;
}
.tools-container li {
  font-size: 30px;
  color: #ffffff;
  padding: 20px;
  align-items: baseline;
  max-width: 560px;
  font-family: var(--text-fonts);
}




.footer-page {
  background-color: var(--white-color);
  color: var(--primary-color);
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  align-items: center;
  height: 100px;
  border-top: 0.1rem solid var(--light-gray-color);
  justify-content: center;
}

.footer-page ul {
  list-style: none;
}

.footer-page ul li a {
  color: var(--primary-color);
  font-size: 1.2rem;
  position: relative;
  text-decoration: none;
}
.footer-page ul li a img{
  max-height: 50px;
  margin: 0 20px 0 20px;
}

.footer-links {
  display:flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 768px){
  .menu ul li a {
    padding: 0.6rem;
    font-size: 0.6rem;
  }

  .menu h1 {
    font-size: 1rem;
  }

  .texto-intro h1 {
    font-size: 3rem;
    width: auto;
  }

  .texto-intro p {
    font-size: 1.5rem;
    padding: 10px 20px;
  }

  .card {
    max-width: 90%;
    margin: 50px auto;
    padding: 20px;
  }

  .card h2 {
    font-size: 1.8rem;
  }

  .card p {
    font-size: 1rem;
  }

  .tools-container h2 {
    font-size: 2rem!important;
    line-height: 2.2rem;
    letter-spacing: 3px;
  }

  .container {
    flex-direction: column;
    align-items: center;
  }
  .card {
    width: 95%;
    max-width: 400px;
    margin: 20px 0;
    padding: 15px;
  }
  .tools-container {
    width: 95%;
    padding: 15px;
  }
  .container-envio * {
    width: 100%;
    font-size: 1rem;
  }
  .container-tabela table {
    font-size: 1rem;
  }

  @media screen and (max-width: 480px) {
  .texto-intro h1 {
    font-size: 2rem;
  }
  .texto-intro p {
    font-size: 1rem;
    padding: 5px;
  }
  .menu h1 {
    font-size: 0.6rem;
    padding: 0.6rem;
  }
  .menu ul li a {
    padding: 0.5rem;
    font-size: 0.9rem;
  }
}
.container-tabela {
    overflow-x: auto;
  }
  .container-tabela table {
    min-width: 500px;
  }
  .botao-padrao,
  .btn-envio {
    width: 100%;
    font-size: 1.1rem;
    height: 45px;
  }
  .input-padrao {
    font-size: 1rem;
    height: 40px;
    padding: 0 10px;
  }
}
